.wrapper {
  background-color: #4055bf;
  align-items: center;
  justify-items: center;
  padding: 10px;
  display: grid;
}

.search-form {
  border-radius: 3px;
  justify-content: center;
  display: flex;
}

.search-form:focus-within {
  outline: 1px solid #3bf;
}

.search-bar {
  width: 300px;
  height: 30px;
  border: none;
  border-radius: 3px 0 0 3px;
  padding: 10px;
}

.search-button {
  width: 30px;
  cursor: pointer;
  border: none;
  border-radius: 0 3px 3px 0;
}

.search-bar:focus-visible, .search-button:focus-visible {
  outline: 1px solid #3bf;
}

